* {
    font-family: 'Roboto'; }
header {
    position: relative; }
nav {
    height: 53px;
    background: #212121; }
.container {
    padding: 0; }
.menu {
    padding: 14.5px 70px;
    margin: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    .menu_item {
        border-right: 2px dotted black;
        padding-right: 28px;
        .menu_link {
            font-weight: 300;
            font-size: 15px;
            color: #FFFFFF; } } }
.subheader {
    position: absolute;
    background-color:rgba(0, 0, 0, 0.6) {}
    width: 100%;
    height: 84px;
    .subheader_logo {
        display: block;
        min-width: 100%;
        margin-top: 17px; }
    .subheader_official {
        color: rgba(255, 255, 255, 0.5);
        font-size: 13px;
        font-style: normal;
        font-weight: 100;
        margin-top: 6px; }
    .subheader_call {
        color: #FFF;
        text-align: right;
        font-size: 12px;
        font-style: normal;
        font-weight: 300;
        margin-top: 22px;
        line-height: 9px; }
    .subheader_phone {
        display: block;
        text-align: right;
        color: #FFF;
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        text-transform: uppercase;
        line-height: 15px;
        margin-top: 8px; }
    .subheader_btn {
        margin-top: 19px;
        text-transform: uppercase;
        color: #FFF;
        text-align: center;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        width: 172px;
        height: 46px;
        border: none;
        border-radius: 4px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset; } }
.promo {
    min-height: 800px;
    height: 800px;
    background: url(../img/bg/uber_bg.png) center center/cover no-repeat;
    padding: 150px 0 135px 0;
    .promo_header {
        color: #FFF;
        text-align: center;
        font-size: 60px;
        font-weight: 900;
        text-transform: uppercase;
        line-height: 60px;
        margin: 0;
        margin-top: 60px; }
    .promo_subheader {
        color: #FFF;
        text-align: center;
        font-size: 36px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 28px;
        margin: 0;
        margin-top: 22px; }
    .promo_descr {
        color: #FFF;
        text-align: center;
        font-size: 18px;
        font-weight: 200;
        line-height: 24px;
        margin-top: 23px; }
    .promo_btn {
        display: block;
        margin: 115px auto 0 auto;
        text-transform: uppercase;
        color: #FFF;
        text-align: center;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        font-size: 18px;
        font-weight: 400;
        width: 259px;
        height: 63px;
        border: none;
        border-radius: 4px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset; } }
.label {
    margin: 0 auto;
    height: 27px;
    width: 115px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }

